@mixin g($color1, $color2) {
  background-color: $color1;
  background-image: linear-gradient($color1, $color2); }

@mixin ellipsis {
  overflow: hidden;
  text-overflow: ellipsis; }

@mixin fxc {
  display: flex;
  justify-content: center;
  align-items: center; }

@mixin screen($res-min, $res-max) {
  @media screen and (min-width: if(unitless($res-min), $res-min, $res-min)) and (max-width: if(unitless($res-max), $res-max, $res-max)) {
    @content; } }

// more than X
@mixin rm($width) {
  @media screen and (min-width: $width + 'px') {
    @content; } }

// height, less than X
@mixin rh($height) {
  @media screen and (max-height: $height + 'px') {
    @content; } }

@mixin font($fz: $fontSize) {
  font-size: $fz * 0.9;
  font-weight: 700;
  line-height: 1.15;

  @include rm($iphonePlus) {
    font-size: $fz;
    line-height: 1.5; } }
